<template>
    <div id="invite_details" :style="{backgroundImage:'url('+img+')'}">
      <img src="../../img/bg1.png" alt="" class="caizhi">
      <!---->
      <div class="inviteBg">
        <div class="invite inviteTop" v-for="(lists,i) in list">
          <ul>
            <li style="text-align: center;">
              <div class="circle"></div>
              <p class="name">{{lists.username}}</p></li>
            <li>
              <div class="circle"></div>
              <div class="phoneInfo">
                <!--<p class="phone">131*******1111</p>-->
                <p class="phone">{{lists.phone}}</p>
                <p class="money"> <span>{{lists.orderNumber}}</span></p>
              </div>
            </li>
            <li>
              <div class="circle"></div>
              <div class="phoneInfo">
                <p class="phone">TA<span>{{lists.invitedNumber}}</span></p>
                <p class="money"> <span>{{lists.sumNumber}}</span></p>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <!---->
      <div class="share"></div>
    </div>
</template>

<script>
  import { Toast } from "mint-ui";
  import axios from 'axios'
  import qs from 'qs'
  import wx from "weixin-js-sdk";
    export default {
        name:'invite_details',
        data(){
          return{
            list:[],
            sumNumber:"",
            orderNumber:"",
            username:"",
            phone:"",
            invitedNumber:"",
            img:require('../../img/bg1-1.png'), 
            sendOpenid:''
          }
        },
        methods:{
            //
          goBack(){
            this.$router.push({path:'/'});
          },
          getInviteDetails(){
            var $this = this;
            var baseUrl=BaseUrl+"/api/invitedInfo";
            var data=qs.stringify({
              openid:localStorage.getItem('openid'),
              several:this.$route.query.several,
            });
            console.log(data)
            axios({
              method: "post",
              url: baseUrl,
              type: 'json',
              data:data
            }).then(function (data) {
              console.log(data);
             
              let datas=data.data.data;
              if(data.data.status==1){
//                  alert(data.data.msg)
                $this.list=datas;
            
              }else{
                Toast({
                  message: datas.msg,
                  duration: 1500
                });
              }
            }).catch(function () {
              //alert("")
            });
          },
          //
         WxZd() {
        //
        const openid = localStorage.getItem('openid');
        
        var data = qs.stringify({
          originUrl: window.location.href
        });
        console.log(data)
        var baseUrl = BaseUrl + "index/getSignPackage";
        axios({
          method: "POST",
          url: baseUrl,
          type: "json",
          data: data
        }).then(
          function(res) {
            var $this=this
            var info = res.data.data.signPackage;
            this.appId = info.appId;
            this.nonceStr = info.nonceStr;
            this.timestamp = info.timestamp;
            this.signature = info.signature;
            wx.config({
              debug: false,
              appId: this.appId, // Ticke------
              timestamp: this.timestamp, // 
              nonceStr: this.nonceStr, // 
              signature: this.signature, // 1
              //:
              jsApiList: ["onMenuShareAppMessage", "onMenuShareTimeline"]
            });

            //
            wx.error(function(res) {
              // logUtil.printLog(":", res);
              console.log(res);
            });
             wx.ready(function() {
        // 
        wx.onMenuShareAppMessage({
          title: "", // 
          desc: "", // 
                link: "http://egg.wufuapp.com?openid=" + $this.sendOpenid + "&makeMoney=100&onlyMine=100",
          imgUrl:
            "http://biuimage.oss-cn-qingdao.aliyuncs.com/uploads/ueditor/image/20180912/1536745700851746.png", // 
          type: "", // ,musicvideolinklink
          dataUrl: "", // typemusicvideo
          success: function() {
            console.log("");
          },
          cancel: function() {
            console.log("");
            // 
          },
          fail: function(res) {
            window.alert(JSON.stringify(res));
          }
        });
        // 
        wx.onMenuShareTimeline({
          title: "", // 
          link: "http://egg.wufuapp.com?openid=" + $this.sendOpenid + "&makeMoney=100&onlyMine=100",
          imgUrl:
            "http://biuimage.oss-cn-qingdao.aliyuncs.com/uploads/ueditor/image/20180912/1536745700851746.png", // 
          success: function() {
            console.log("");
          },
          cancel: function() {
            console.log("");
          },
          fail: function(res) {
            window.alert(JSON.stringify(res));
          }
        });
      });
          }.bind(this)
        );
      },
    },
        created(){
          document.title="";
        },
        mounted(){
          this.getInviteDetails();
          this.sendOpenid=localStorage.getItem('openid')
          this.WxZd()
          // this.wxShare('');
          // this.wxShare('');
          },
        components:{}
    }
</script>

<style lang="scss" scoped>
#invite_details{
  background: #E9844D;
  min-height: 41.6rem;
  .caizhi{
    width: 100%;
    display: block;
  }
  .inviteBg {
    min-height: 15.6rem;
    background-size: 23.6rem;
    .invite {
      background: #fff;
      width: 90%;
      margin: 0 auto;
      margin-top: .2rem;
      display: flex;
      align-items: center;
      border-radius: .3rem;
      ul {
        width: 100%;
        margin: .5rem 0;
        list-style-type: none;
        padding: 0;
        overflow: hidden; /**/
        white-space: nowrap; /**/
        text-overflow: ellipsis;
        li {
          font-size: 1.5rem;
          display: flex;
          align-items: center;
          .name {
            width: 100%;
            font-size: 1rem;
            text-align: center;
          }
          .circle {
            width: .6rem;
            height: .6rem;
            margin: 0 .6rem;
            border-radius: 50%;
            background: #E9844D;
          }
          .phoneInfo {
            width: 100%;
            display: flex;
            align-items: center;
            color: #666666;
            font-size: .9rem;
            .phone {
              width: 50%;
            }
            .money {
              width: 50%;
              text-align: right;
              padding-right: .5rem;
            }
            span {
              color: #CC3E36;
            }
          }
        }
      }
    }
    .inviteTop {
      margin-top: 0.5rem;
    }
  }
  /**/
  .share{
    width:13.5rem;
    font-size: 1.3rem;
    line-height: 3.5rem;
    background: #EF3C69;
    color: #fff;
    border-radius: 2rem;
    margin: 0 auto;
    margin-top: 6rem;
    text-align: center;
  }
}
</style>



